<template>
  <div class="lnglat">
    <el-row>
      <el-col :span="12" :offset="6">
        <h2>点1</h2>
        <div>
          <span>经度：<el-input v-model="lng1" placeholder="请输入经度"></el-input>(0-180)</span>
          纬度：<el-input v-model="lat1" placeholder="请输入纬度"></el-input>(0-90)
        </div>
        <h2>点2</h2>
          <div>
          <span>经度：<el-input v-model="lng2" placeholder="请输入经度"></el-input>(0-180)</span>
          纬度：<el-input v-model="lat2" placeholder="请输入纬度"></el-input>(0-90)
        </div>
        <el-button plain @click="getDistance">计算距离</el-button>

        <div v-if="distance" class="distance-wrapper">两点间距离：{{distance}}米</div>
        </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      lng1: "",
      lat1: "",
      lng2: "",
      lat2: "",
      distance: 0
    }
  },
  methods: {
    getRad(d){
      var PI = Math.PI;
        return d*PI/180.0;
    },
    getDistance() {
      var f = this.getRad((this.lat1 + this.lat2)/2);
        var g = this.getRad((this.lat1 - this.lat2)/2);
        var l = this.getRad((this.lng1 - this.lng2)/2);
        
        var sg = Math.sin(g);
        var sl = Math.sin(l);
        var sf = Math.sin(f);
        
        var s,c,w,r,d,h1,h2;
        var a = 6378137.0;
        var fl = 1/298.257;
        
        sg = sg*sg;
        sl = sl*sl;
        sf = sf*sf;
        
        s = sg*(1-sl) + (1-sf)*sl;
        c = (1-sg)*(1-sl) + sf*sl;
        
        w = Math.atan(Math.sqrt(s/c));
        r = Math.sqrt(s*c)/w;
        d = 2*w*a;
        h1 = (3*r -1)/2/c;
        h2 = (3*r +1)/2/s;
        
        this.distance = d*(1 + fl*(h1*sf*(1-sg) - h2*(1-sf)*sg));
    }
  }
}
</script>

<style scoped lang="scss">
.lnglat{
  padding-top: 80px;
}
.el-input{
  width: 200px !important;
}
.el-button{
  margin: 50px 0 50px;
}
.el-col{
  margin-top: 30px;
  span {
    margin-right: 40px;
  }
}
.distance-wrapper{
  font-size: 18px;
  color: #468847;
}
</style>



