<template>
  <div class="timeStamp">
    <el-row>
      <el-col :span="12" :offset="6">
        <div class="item">
          <span>时间</span>
          <el-input v-model="timeVal" placeholder="请输入时间"></el-input>
          <el-button plain @click="getTimeStamp">》</el-button>
          <el-input v-model="timeStamp" placeholder="请输入时间戳"></el-input>
          <span>时间戳</span>
        </div>
        <div class="item">
          <span>时间戳</span>
          <el-input v-model="timeStamp2" placeholder="请输入时间戳"></el-input>
          <el-button plain @click="getTimeStamp2">》</el-button>
          <el-input v-model="timeVal2" placeholder="请输入时间"></el-input>
          <span>时间</span>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import dateFormat from "dateformat"
export default {
  name: 'timeStamp',
  data () {
    return {
      timeStamp: "",
      timeVal: dateFormat(new Date(),"yyyy-mm-dd HH:MM:ss"),
      timeStamp2: "",
      timeVal2: ""
    }
  },
  methods: {
    getTimeStamp() {
      this.timeStamp = new Date(this.timeVal).getTime();
    },
    getTimeStamp2() {
      if (!this.timeStamp2.trim()){
        alert("请输入时间戳")
      } else {
        this.timeVal2 = dateFormat(parseInt(this.timeStamp2),"yyyy-mm-dd HH:MM:ss")
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .timeStamp{
    padding-top: 120px;
  }
  .el-input{
    width: 200px;
  }
  .item{
    margin-top: 25px;
    span{
      width: 60px;
      display: inline-block;
    }
  }
</style>



