<template>
  <div class="regular">
      <el-row>
      <el-col :span="12" :offset="6">
        <h2>手机号</h2>
        <p>/^1((3[\d])|(4[5,6,9])|(5[0-3,5-9])|(6[5-7])|(7[0-8])|(8[1-3,5-8])|(9[1,8,9]))\d{8}$/</p>
        <h2>大写字母</h2>
        <p>/^[A-Z]+$/</p>
        <h2>日期,如: 2000-01-01</h2>
        <p>/^\d{4}(-)\d{1,2}\1\d{1,2}$/</p>
        <h2>email地址</h2>
       <p>/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/</p>
        <h2>国内座机电话,如: 0341-86091234</h2>
       <p>/\d{3}-\d{8}|\d{4}-\d{7}/</p>
        <h2>身份证号(15位、18位数字)，最后一位是校验位，可能为数字或字符X</h2>
       <p>/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/</p>
        <h2>帐号是否合法(字母开头，允许5-16字节，允许字母数字下划线组合</h2>
       <p>/^[a-zA-Z][a-zA-Z0-9_]{4,15}$/</p>
        <h2>只包含中文</h2>
       <p>/^[\u4E00-\u9FA5]/</p>
        <h2>是否小数</h2>
       <p>/^\d+\.\d+$/</p>
        <h2>是否电话格式(手机和座机)</h2>
       <p>/^((0\d{2,3}-\d{7,8})|(1[345789]\d{9}))$/</p>
        <h2>是否8位纯数字</h2>
       <p>/^[0-9]{8}$/</p>
        <h2>是否html标签</h2>
       <p>/<(.*)>.*<\/\1>|<(.*) \/>/</p>
        <h2>是否qq号格式正确</h2>
       <p>/^[1-9]*[1-9][0-9]*$/</p>
        <h2>是否由数字和字母组成</h2>
       <p>/^[A-Za-z0-9]+$/</p>
        <h2>是否小写字母组成</h2>
       <p>/^[a-z]+$/</p>
        <h2>密码强度正则，最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符</h2>
       <p>/^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/</p>
        <h2>用户名正则，4到16位（字母，数字，下划线，减号）</h2>
       <p>/^[a-zA-Z0-9_-]{4,16}$/</p>
        <h2>ipv4地址正则</h2>
       <p>/^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/</p>
        <h2>16进制颜色</h2>
       <p>/^#?([a-fA-F0-9]{6}|[a-fA-F0-9]{3})$/</p>
        <h2>微信号，6至20位，以字母开头，字母，数字，减号，下划线</h2>
       <p>/^[a-zA-Z]([-_a-zA-Z0-9]{5,19})+$/</p>
        <h2>中国邮政编码</h2>
       <p>/^(0[1-7]|1[0-356]|2[0-7]|3[0-6]|4[0-7]|5[1-7]|6[1-7]|7[0-5]|8[013-6])\d{4}$/</p>
        <h2>只包含中文和数字</h2>
       <p>/^(([\u4E00-\u9FA5])|(\d))+$/</p>
        <h2>非字母</h2>
       <p>/[^A-Za-z]/</p>
      </el-col>
      </el-row>
  </div>
</template>

<script>
export default {
  name: 'regular',
  data () {
    return {
    }
  }
}
</script>

<style scoped lang="scss">
  .regular{
    padding-top: 110px;
  }
  h2{
    font-weight: 600;
    cursor: text;
    margin-bottom: 15px;
    font-size: 1.2em;
    color: rgb(68, 84, 149);
    line-height: 1.25;
    text-align: start;
    white-space: normal
  }
  p{
    margin-right: 2px;
    margin-left: 2px;
    padding-right: 5px;
    padding-left: 5px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(234, 234, 234);
    background-color: rgb(248, 248, 248);
    border-radius: 3px;
    font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
    font-size: 16px;
    line-height: 18px;
    color: rgb(221, 17, 68);
    font-size: 15px
  }
</style>



