<template>
  <div class="qrcode">
    <el-row>
      <el-col :span="12" :offset="6">
        <div class="qrcode-wrapper">
          <div class="content">
            <el-input
              type="textarea"
              :rows="9"
              placeholder="请输入内容"
              v-model="textarea">
            </el-input>
            <el-button type="primary" @click="getQrCode">生成二维码</el-button>
          </div>
          <div class="img-wrapper">
            <div id="qrcode" ref="qrcode"></div>
            <el-button type="primary" @click="downLoadQrCode">下载二维码</el-button>
          </div>
        </div>
        <div>
          
        </div>
      </el-col>
    </el-row>
  </div>
</template>
// <script src="http://libs.baidu.com/jquery/2.0.0/jquery.min.js"></script>
<script>
import QRCode from "qrcodejs2";
export default {
  name: 'qrcode',
  data () {
    return {
      textarea: ""
    }
  },
  methods: {
    qrcode () {
      let qrcode = new QRCode("qrcode", {
        width: 200, // 二维码宽度，单位像素
        height: 200, // 二维码高度，单位像素
        text: this.textarea // 生成二维码的链接
      });
    },
    getQrCode() {
      if (!this.textarea.trim()) {
        alert("请输入内容")
        return
      }
      this.$nextTick(() => {
        document.getElementById('qrcode').innerHTML = '';
        this.qrcode();
      });
    },
    downLoadQrCode() {
      if(!this.$refs.qrcode.children.length){
        alert("请输入内容生成二维码")
        return
      }else{
        var canvasData = this.$refs.qrcode.children[1].src;
        var a = document.createElement("a");
        a.href = canvasData;
        a.download = "drcQrcode";
        a.click();
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .qrcode{
    padding-top: 110px;
  }
  .qrcode-wrapper{
    display: flex;
  }
  .content{
    flex: 5;
    padding: 20px 0;
  }
  .img-wrapper{
    flex: 2;
    padding: 20px;
    #qrcode{
      border-radius: 5px;
      background: #f5f5f5;
      width: 200px;
      height: 200px;
    }
  }
  .el-button{
    margin-top: 20px;
  }
</style>



